/*@import 'fonts';*/
body {
  font-family: "Noto Sans", sans-serif;
  font-size: 17px;
  font-style: normal;
  font-weight: normal;
  line-height: 32px;
}

@media (max-width: 768px) {
  .container {
    padding: 0 10px;
  }
}
.block-background {
  background-color: #FBFBFC;
}

.title {
  color: #1652AC;
  letter-spacing: 2.42px;
  font-style: normal;
  font-weight: bold;
  font-size: 20px;
  line-height: 40px;
}

.title-dark {
  color: #020303;
  font-weight: 500;
  font-size: 28px;
  line-height: 40px;
  letter-spacing: 4.42px;
  text-align: center;
}

.button {
  background: linear-gradient(180deg, #0C5CD6 0%, rgba(0, 56, 140, 0) 100%), #004FC7;
  border: 1px solid #FFFFFF;
  box-sizing: border-box;
  box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.21);
  font-weight: 500;
  font-size: 17px;
  line-height: 40px;
  letter-spacing: 1.42px;
}

.text-bold {
  font-weight: 600;
  color: #020303;
}

.header {
  padding-top: 10px;
}

.navbar-light .navbar-toggler {
  border: none;
}

.nav-link {
  white-space: nowrap;
}

.description {
  padding-top: 103px;
  padding-bottom: 108px;
}
.description__title {
  font-weight: bold;
  font-size: 48px;
  /*line-height: 32px;*/
  letter-spacing: -0.4px;
  color: #1652AC;
  position: relative;
}
.description__text {
  font-weight: 600;
  font-size: 17px;
  line-height: 32px;
  letter-spacing: -0.4px;
  color: #020303;
}
.description__text {
  padding: 55px 0;
  position: relative;
}
.description__text::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  width: 227px;
  height: 7px;
  background: #EDEFF3;
}
.description__text:after {
  position: absolute;
  content: "";
  bottom: 0;
  left: 0;
  width: 227px;
  height: 7px;
  background: #EDEFF3;
}
.description__img2 {
  display: none;
}

@media (max-width: 768px) {
  .description {
    padding-bottom: 80px;
  }

  .description__img {
    display: none;
  }

  .description__img2 {
    display: block;
    width: 354px;
    margin: 0 auto;
  }

  .description__text::before,
.description__text::after {
    display: none;
  }

  .description__title {
    text-align: center;
    padding-bottom: 40px;
  }

  .description__title::after {
    position: absolute;
    content: "";
    bottom: 0;
    left: 32%;
    width: 227px;
    height: 7px;
    background: #EDEFF3;
  }
}
.focus__text {
  font-family: Noto Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 17px;
  line-height: 32px;
}

@media (max-width: 768px) {
  .focus__title {
    text-align: center;
  }
}
.standart {
  margin-bottom: 90px;
}
.standart__img {
  height: 729px;
}
.standart__title {
  color: #fff;
}
.standart__container {
  padding: 0;
  margin: 0 auto;
  background: #163DA1;
}
.standart__content {
  color: #fff;
  padding-top: 151px;
}

@media (max-width: 768px) {
  .standart {
    padding-top: 32px;
  }

  .standart__content {
    padding: 0 10px;
  }
}
.specialization {
  margin-bottom: 127px;
}

.specialization__img {
  width: 666px;
  padding-bottom: 75px;
  padding-top: 30px;
  margin-left: auto;
  border-bottom-left-radius: 25px;
  -webkit-box-shadow: -1px 19px 24px -14px rgba(140, 148, 154, 0.23);
  -moz-box-shadow: -1px ​19px 24px -14px rgba(140, 148, 154, 0.23);
  box-shadow: -1px 19px 24px -14px rgba(140, 148, 154, 0.23);
}

.specialization__img-line {
  width: 616px;
  height: 104px;
  border-bottom-left-radius: 16px;
  border-top-left-radius: 16px;
  background: #ff3d3d;
  background: linear-gradient(97deg, #ff3d3d 26%, #3535ff 100%);
  margin-left: auto;
}

.specialization__img-line:not(:last-child) {
  margin-bottom: 46px;
}

@media (max-width: 768px) {
  .specialization {
    margin-bottom: 80px;
  }

  .specialization__img {
    width: 100%;
  }

  .specialization__img-line {
    width: 100%;
    margin: auto;
    border-radius: 0;
  }

  .specialization__title {
    text-align: center;
  }

  .specialization__content {
    padding-top: 56px;
  }

  .specialization__text p:first-child {
    font-style: italic;
  }

  .specialization__content {
    order: 2;
  }

  .specialization__img-block {
    order: 1;
  }
}
.advantage {
  padding-top: 67px;
  padding-bottom: 32px;
}
.advantage__content {
  margin: -9px;
  padding-top: 42px;
}

.advantage__item {
  padding: 0px 39px;
  background: #FFFFFF;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.15);
  border-radius: 16px;
  flex: 0 1 31.5%;
  min-height: 136px;
  margin: 9px;
}

.advantage__img {
  margin-right: 38px;
}

@media (max-width: 1100px) {
  .advantage__item {
    flex: 47%;
  }
}
@media (max-width: 768px) {
  .advantage__item {
    flex: 100%;
  }
}
.features {
  padding-bottom: 44px;
}
.features__container {
  background: #FFFFFF;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.15);
  border-radius: 16px;
  padding: 43px 62px 43px 12px;
}
.features__item {
  margin-bottom: 48px;
}
.features__num {
  margin-left: 38px;
  border-radius: 50%;
  flex-shrink: 0;
  color: #45494F;
  font-weight: bold;
  font-size: 17px;
  line-height: 35px;
  letter-spacing: -0.4px;
  width: 22px;
  height: 22px;
  border: 1px solid #45494F;
  margin-right: 25px;
}
.features__title {
  margin-bottom: 71px;
}
.bilder__container {
  padding-bottom: 185px;
}

.slider {
  margin-top: 31px;
}

.carousel-inner {
  border-radius: 16px;
}

.carousel-item img {
  height: 100%;
  object-fit: cover;
}

.carousel-indicators {
  margin-left: 33px;
}

.carousel-indicators li {
  position: relative;
  -webkit-box-flex: 0;
  -ms-flex: 0 1 auto;
  flex: 0 1 auto;
  width: 12px;
  height: 12px;
  border-radius: 50%;
  margin-right: 3px;
  margin-left: 3px;
  text-indent: -999px;
  background-color: #80ABFF;
  border: 1px solid #0045AC;
  box-sizing: border-box;
}

.carousel-indicators li:hover {
  background-color: #004FC7;
  opacity: 1;
}

.carousel-indicators li:not(:last-child) {
  margin-right: 28px;
}

.slider {
  position: relative;
}

.carousel-indicators {
  position: relative;
}

.carousel-control-prev-icon,
.carousel-control-next-icon {
  background-image: none;
  position: absolute;
  top: 15px;
  left: 45px;
  opacity: 1;
}

.carousel-control-prev {
  margin-right: 18px;
}

.carousel-control-next,
.carousel-control-prev {
  position: relative;
  min-width: 126px;
  height: 78px;
  text-align: center;
  background: #FFFFFF;
  box-shadow: 0px 0px 21px rgba(64, 68, 75, 0.14);
  border-radius: 8px;
  display: block;
}

.carousel-control-buttons {
  display: flex;
}

.carousel__control {
  margin-top: 29px;
}

@media (max-width: 1000px) {
  .bilder__container {
    padding-bottom: 89px;
  }
}
@media (max-width: 768px) {
  .bilder__container {
    padding-bottom: 89px;
  }

  .carousel-control-buttons {
    display: none;
  }

  .carousel-indicators {
    margin: 0 auto;
  }
}
.top {
  border-bottom: 2px solid #EEF0F4;
}
.top__btns-group {
  padding-top: 31px;
  padding-bottom: 82px;
  margin: -8px;
}
.top__btn {
  width: 30%;
  margin: 8px;
}

.btn-group > .btn-group:not(:last-child) > .btn,
.btn-group > .btn:not(:last-child):not(.dropdown-toggle),
.btn-group > .btn-group:not(:first-child) > .btn,
.btn-group > .btn:nth-child(n+3),
.btn-group > :not(.btn-check) + .btn {
  border-radius: 12px;
}

@media (max-width: 1000px) {
  .top__btn {
    width: 47%;
  }
}
@media (max-width: 768px) {
  .top__btn {
    width: 100%;
  }
}
.order {
  padding-bottom: 54px;
  border-bottom: 2px solid #EEF0F4;
}
.order__link {
  white-space: nowrap;
}
.order__title {
  font-size: 34px;
  line-height: 43px;
  padding-top: 30px;
  white-space: nowrap;
}
.order__container {
  padding-top: 73px;
}
.order__btn {
  width: 306px;
  padding: 21px;
  font-weight: 500;
  font-size: 18px;
  line-height: 26px;
  border: 1px solid #FFFFFF;
  border-radius: 12px;
  position: relative;
}
.order__btn svg {
  position: absolute;
  top: 27px;
  left: 0;
}
.order__content {
  padding-right: 30px;
}

@media (max-width: 1200px) {
  .order__title {
    white-space: normal;
    text-align: center;
  }
}
@media (max-width: 768px) {
  .order {
    padding: 0 24px;
  }

  .logo-2 {
    order: 1;
    justify-content: center;
  }

  .order__title-block {
    order: 2;
  }

  .order__content {
    margin-top: 37px;
    order: 3;
  }

  .order__title {
    padding-top: 27px;
  }

  .order__btn {
    width: 100%;
  }
}
.footer {
  padding-top: 49px;
  color: #9EABBB;
}
.footer__item {
  margin-left: 92px;
}
.footer__link {
  color: #9EABBB;
}
.footer__title {
  font-weight: bold;
  font-size: 17px;
  line-height: 188%;
  letter-spacing: -0.4px;
}
.footer__black-text {
  color: #45494F;
  font-size: 17px;
  line-height: 32px;
  letter-spacing: -0.4px;
}
.footer__contact {
  margin: 0 165px 0 207px;
}

@media (max-width: 1000px) {
  .footer__contact {
    margin: 0 20px;
  }

  .footer__item {
    margin-left: 20px;
  }
}
@media (max-width: 768px) {
  .footer__container {
    padding-left: 36px;
    flex-wrap: wrap;
  }

  .footer__container > div {
    width: 100%;
    margin: 0;
    margin-bottom: 34px;
  }
}